<template>
	<header class="main-header">
		<button @click.prevent="toggleMenu" :class="[isOpen ? 'is-open' : '']">
			<menu-icon ref="menuIcon" />
		</button>
	</header>
</template>

<script>
	
	import { EventBus } from '../utils/event-bus';
	import { EVENTS } from '../utils/events';

	import MenuIcon from './MenuIcon';
	
	const { TOGGLE_MENU } = EVENTS;

	export default {
		name: 'AppHeader',
		components: {
			MenuIcon,
		},
		data(){
			return {
				isOpen: false,
			}
		},
		methods: {
			toggleMenu() {

				this.isOpen = ! this.isOpen;
				
				EventBus.$emit( TOGGLE_MENU, this.isOpen );
				
			}
		}
	}
</script>

<style scoped>
	header {
		text-align: right;
		background-color: var(--color__dark-alpha);
		transform: rotate(-0.4deg);
		padding-top: 0.5rem;
	}
	
	button {
		transform: translate(40%, -5%);
	}
</style>



