<template>
	<div>
		<menu-list/>
		<div class="page-wrap">
			<app-header/>
			<main>
				<header class="box-wrapper">
					<h1 id="main-heading" class="box box--header">
						<span id="masthead__name">Alessandro</span>
						<span id="masthead__surname">Mese</span>
					</h1>
					<p class="box box--tagline">
						Creative digital strategist
					</p>
					<main-copy :classes="'box box--main-copy'"/>
				</header>
			</main>
			<app-footer/>
		</div>
	</div>
</template>

<script>
	import AppHeader from './AppHeader';
	import AppFooter from './AppFooter';
	import MainCopy from './MainCopy';
	import MenuList from './MenuList';

	export default {
		name: "Home",
		components: {
			AppHeader,
			MainCopy,
			AppFooter,
			MenuList,
		}
	}
</script>

<style scoped>
	
	@keyframes heading {
		0% {
			background-size: 0 3px,
			3px 0,
			0 3px,
			3px 0;
		}
		25% {
			background-size: 100% 3px,
			3px 0,
			0 3px,
			3px 0;
		}
		50% {
			background-size: 100% 3px,
			3px 100%,
			0 3px,
			3px 0;
		}
		75% {
			background-size: 100% 3px,
			3px 100%,
			100% 3px,
			3px 0;
		}
		100% {
			background-size: 100% 3px,
			3px 100%,
			100% 3px,
			3px 100%;
		}
	}
	
	h1,
	p {
		color: var(--color__dark);
	}
	
	h1 {
		font-size: 4vw;
		background-repeat: no-repeat;
		background-image: linear-gradient(to right, var(--color__dark) 100%, var(--color__dark) 100%),
					linear-gradient(to bottom, var(--color__dark) 100%, var(--color__dark) 100%),
					linear-gradient(to right, var(--color__dark) 100%, var(--color__dark) 100%),
					linear-gradient(to bottom, var(--color__dark) 100%, var(--color__dark) 100%);
		background-size: 100% 3px,
				3px 100%,
				100% 3px,
				3px 100%;
		background-position: 0 0,
				100% 0,
				100% 100%,
				0 100%;
		animation: heading 1.25s cubic-bezier(0.19, 1, 0.22, 1) 1;
		/*animation-play-state: paused;*/
		grid-area: sidebar;
		margin: auto;
		padding: 0.3em 0.5em;
		position: relative;
		z-index: 2;
	}
	
	h1::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		height: 98%;
		width: 99%;
		background-color: var(--color__dark-alpha);
		transform: translate(-2%, 5%);
		border-radius: 0 / 20px ;
	}
	
	span {
		display: block;
	}
	
	p {
		font-size: 3vw;
	}
	
	.box-wrapper {
		display: grid;
		grid-gap: 3rem;
		/*grid-template-columns: 120px 120px 120px;*/
		grid-template-areas:
				"....... header  header"
							  "sidebar content content";
	}
	
	.box--tagline {
		grid-area: header;
	}
	
	.box--main-copy {
		grid-area: content;
	}
	
	@media screen and (min-width: 64em) {
		.box--tagline {
			text-align: right;
		}
	}
	
</style>



