import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/components/Home';

const Philadelphia = () => import( '@/components/Philadelphia.vue' );
const Bayer = () => import( '@/components/Bayer.vue' );
const Sisal = () => import( '@/components/Sisal.vue' );
const CheBanca = () => import( '@/components/CheBanca.vue' );
const Yamaha = () => import( '@/components/Yamaha.vue' );

Vue.use( Router );

export default new Router( {
	mode: 'history',
	routes: [
		{
			path: '/',
			name: 'Home',
			component: Home
		},
		{
			path: '/philadelphia',
			name: 'philadelphia',
			component: Philadelphia,
		},
		{
			path: '/bayer',
			name: 'bayer',
			component: Bayer,
		},
		{
			path: '/sisal',
			name: 'sisal',
			component: Sisal,
		},
		{
			path: '/che-banca',
			name: 'che-banca',
			component: CheBanca,
		},
		{
			path: '/yamaha',
			name: 'yamaha',
			component: Yamaha,
		},
	]
} )



