		<rect x="0" y="20" width="60" height="7"></rect>
		<rect x="0" y="40" width="60" height="7"></rect>
	</svg>
</template>

<script>
	export default {
		name: 'MenuButton',
	}
</script>

<style scoped>
	svg {
		height: 30px;
	}
	
	rect {
		height: 2px;
		fill: var(--color__dark);
	}
	
	.is-open svg {
		transform: rotate(90deg);
	}
</style>



