<template>
	<nav :class="[isOpen === true ? 'is-open' : '']">
		<router-link to="/philadelphia">
			<span>Philadelphia</span>
		</router-link>
		<router-link to="/che-banca">
			<span>CheBanca!</span>
		</router-link>
		<router-link to="/bayer">
			<span>Bayer</span>
		</router-link>
		<router-link to="/sisal">
			<span>Sisal</span>
		</router-link>
		<router-link to="/yamaha">
			<span>Yamaha</span>
		</router-link>
	</nav>
</template>

<script>
	import { EventBus } from '../utils/event-bus';
	import { EVENTS } from '../utils/events';

	const { TOGGLE_MENU } = EVENTS;
	
	export default {
		name: 'MenuList',
		data() {
			return {
				isOpen: false,
			}
		},
		mounted() {

			EventBus.$on( TOGGLE_MENU, payload => {
				
				this.isOpen = payload;
				

			} );
		}
	}
</script>

<style scoped>
	@keyframes sliding {
		0%,
		100% {
			transform: scaleX(0);
		}
		
		55% {
			transform: scaleX(1);
		}
		
		57% {
			transform: scaleX(0.95);
		}
		
		59% {
			transform: scaleX(1);
		}
		
		100% {
			transform-origin: right center;
		}
	}
	
	@keyframes reflow {
		to {
			width: auto;
		}
	}
	
	nav {
		background-color: var(--color__complementary);
		padding-right: 3em;
		padding-left: 3em;
		box-shadow: inset 0 16px 17px 2px var(--color__light-alpha);
		position: relative;
		z-index: 2;
		clip-path: polygon(0 0, 100% 0, 100% 100%, 0 95%);
		transition-property: opacity, transform, max-height;
		transition-duration: 0.35s, 0.5s, 0.25s;
		/*transition-timing-function: var(--ease-in-out-circle);*/
		transition-timing-function: ease-out;
		opacity: 0;
		max-height: 0;
		transform: translate3d(0, -100%, 0);
		will-change: opacity, max-height;
		overflow: hidden;
	}
	
	nav.is-open {
		opacity: 1;
		max-height: 900px;
		transform: translate3d(0, 0, 0);
		padding-top: 2em;
		padding-bottom: 2em;
	}
	
	a {
		display: block;
		text-align: center;
		border-bottom: 0;
		font-size: 1.75vw;
		color: var(--color__light);
		opacity: 0.8;
	}
	
	span {
		position: relative;
		display: inline-block;
	}
	
	span::after {
		content: '';
		position: absolute;
		left: 0;
		bottom: 6px;
		width: 100%;
		height: 1px;
		background-color: var(--color__light-alpha-norm);
		animation: sliding 1s var(--ease-in-out-circle) forwards alternate;
		animation-play-state: paused;
		transform-origin: left center;
	}
	
	.no-touchevents span:hover::after {
		animation-play-state: running;
	}
	
	a:not(:first-child) {
		margin-top: 0.75em;
	}
	
	a:not(:last-child) {
		margin-bottom: 0.75em;
	}
	
	.no-touchevents a:hover::after {
		display: none;
	}
</style>



